


import {
    React,
    connect,
    Component,
    reduxForm,
    Field,
    InputDate,
    Tabel,
    Loading,
    getTglSystem
  } from "../../../../components/helpers/library";
class HeadFormNonCash extends Component {
  constructor(props) {
    super(props);
    this.state = {
      date: new Date(),
    };
    this.handleSubmit = this.handleSubmit.bind(this);
    this.setStartDate = this.setStartDate.bind(this);
    this.setLastDate = this.setLastDate.bind(this);
  }

  handleSubmit(data) {
    console.log(data);
  }
  exportPdf() {
    // LaporanDaraMemberPdf(this.props.datamember);
  }
  setStartDate(date) {
    this.setState({
      tgl_awal: new Date(date),
    });
  }
  setLastDate(date) {
    this.setState({
      tgl_akhir: new Date(date),
    });
  }

  render() {
    let datamemeber = [];
    this.props.datamember.forEach((row, index) => {
      row.detail.forEach((item, index) => {
        datamemeber.push(item);
      });
    });
    let columns = [
        {
            dataField: "kategori",
            text: "Kategori",
            formatter: (data) => {
              return data === undefined ? "-" : data;
            },
            csvFormatter: (data) => {
              return data === undefined ? '-' : data;
            },
            footerAttrs: {
              'colSpan': '2'
            },
            footer: "Total"
          },
          {
            dataField: "deskripsi",
            text: "Deskripsi",
            formatter: (data) => {
              console.log(data);
                 // console.log(data);
              let row = data || "-";
              let hasil = row.split('<br/>');
              return data === undefined ? "-" : <> {hasil[1]} <br/> {hasil[2]} </>;
            },
            csvFormatter: (data) => {
              return data === undefined ? '-' : data;
            },
          },
          {
            dataField: "keterangan",
            text: "Keterangan",
            formatter: (data) => {
              console.log(data);
                 // console.log(data);
              let row = data || "-";
              let hasil = row.split('<br/>');
              return data === undefined ? "-" : <> {hasil[1]} <br/> {hasil[2]} </>;
            },
            csvFormatter: (data) => {
              return data === undefined ? '-' : data;
            },
          },
          {
            dataField: "jumlah_in",
            text: "Uang Masuk",
            headerClasses: 'text-right',
            formatter: (data) => {
              return <div className="text-right"> {data === undefined || null ? 0 : parseFloat(data).toLocaleString("id-ID")}</div>;
            },
            csvFormatter: (data) => {
              return <div className="text-right"> {data === undefined || null ? '0' : data} </div>
            },
            footer: (columnData) =>
            <div className="text-right"> {parseInt(
              columnData.reduce((acc, item) => acc + item, 0)
            ).toLocaleString("id-ID")} </div>
          },
          {
            dataField: "jumlah_out",
            text: "Uang Keluar",
            headerClasses: 'text-right',
            formatter: (data) => {
              return <div className="text-right"> {data === undefined || null ? 0 : parseFloat(data).toLocaleString("id-ID")}</div>;
            },
            csvFormatter: (data) => {
              return <div className="text-right"> {data === undefined || null ? '0' : data} </div>
            },
            footer: (columnData) =>
            <div className="text-right"> {parseInt(
              columnData.reduce((acc, item) => acc + item, 0)
            ).toLocaleString("id-ID")} </div>
          },
    ];

    return (
      <form onSubmit={this.props.handleSubmit} autoComplete="off">
        <div className="row">
          <div className="col-lg-4">
            <Field
              name="tgl_awal"
              component={InputDate}
              label="Tanggal Dari"
              type="text"
              selected={this.state.tgl_awal}
              onChange={(date) => this.setStartDate(date)}
              placeholder="Masukan Tanggal Dari"
            />
          </div>

          <div className="col-lg-4">
            <Field
              name="tgl_akhir"
              component={InputDate}
              type="text"
              selected={this.state.tgl_akhir}
              onChange={(date) => this.setLastDate(date)}
              label="Tanggal Akhir"
              placeholder="Masukan Tanggal Akhir"
            />
          </div>

          <div className="col-lg-4">
            <div className="text-right">
              <label>&nbsp;</label>
              <button
                type="submit"
                value="PDF"
                name="type_btn"
                className="btn btn-primary btn-block"
              >
                {this.props.isLoading ? (
                  <>
                    <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                    Mencari
                  </>
                ) : (
                  "Cari Laporan"
                )}
              </button>
            </div>
          </div>
          <div className="col-lg-12">
            <Tabel
              keyField="no_hp"
              data={this.props.export ? datamemeber : []}
              columns={columns}
              empty={true}
              textEmpty="Data Laporan Hutang Kosong"
            />
          </div>
          <div className={this.props.export ? "col-lg-12" : "col-lg-12 d-none"}>
            <label htmlFor="">&nbsp;</label>
            <div className="row">
              <div className="col-lg-6">
                <button
                  onClick={() => this.exportPdf()}
                  className="btn btn-warning btn-block"
                >
                  {" "}
                  Export PDF{" "}
                </button>
              </div>
              <div className="col-lg-6">
                {/* <LaporanDataMemberExel data={this.props.datamember} /> */}
              </div>
            </div>
          </div>
        </div>
        {this.props.isLoading ? (
        <Loading 
        loading
        background="rgba(0, 0, 0, 0.35)"
         loaderColor="rgba(94, 147, 117, 1)" />
      ) : null}
      </form>
    );
  }
}

HeadFormNonCash = reduxForm({
  form: "HeadFormNonCash",
  enableReinitialize: true,
})(HeadFormNonCash);
// export default connect()(HeadFormNonCash);
export default connect((state) => {
  return {
    initialValues: {
      tgl_awal: getTglSystem(),
      tgl_akhir: getTglSystem(),
    },
  };
})(HeadFormNonCash);
